const login = () => import('@/views/login/login');
const bindInfo = () => import('@/views/login/register/bindInfo');
const regByOrder = () => import('@/views/login/register/reg-by-order');
const shops = () => import('@/views/login/register/shops');
const regInvite = () => import('@/views/login/invite/');
const follow = () => import('@/views/login/follow/');

export default [
  {
    path: "/login",
    name: "login",
    component: login,
    meta:{
      notLogin:true
    }
  },
  {
    path: "/login/bindInfo",
    name: "bindInfo",
    props: true,
    component: bindInfo,
    meta:{
      notLogin:true
    }
  },
  {
    path: "/login/regByOrder",
    name: "regByOrder",
    props: true,
    component: regByOrder,
    meta: {
      notLogin:true
    }
  },
  {
    path: "/login/shops",
    name: "shops",
    props: true,
    component: shops,
    meta: {
      notLogin:true
    }
  },
  {
		path: "/login/invite/:invite_code/:auth_time/:auth_code",
		name: "regInvite",
		component: regInvite,
    props: true,
    meta: {
      notLogin:true
    }
  },
  {
		path: "/login/follow",
		name: "follow",
    component: follow,
    meta: {
      notLogin:true
    }
}]



