const tab_home = () => import('@/views/home');
const Notice = () => import('@/views/home/notice.vue');
const Tabbar = () => import('@/vue/components/Tabbar/');
const Invite = () => import('@/views/home/invite.vue');
const WxError = () => import('@/views/home/wxerror.vue');

export default [
  {
    path: "/",
    name: "index",
    components: {
      default: tab_home,
      tabbar: Tabbar,
    },
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/home/notice',
    name: 'notice',
    component: Notice,
  },
  {
    path: '/home/invite',
    name: 'invite',
    component: Invite
  },
  {
    path: '/home/wxerror',
    name: 'wxerror',
    component: WxError,
    meta:{
      notLogin:true
    }
  }
]



