const Team = () => import(/* webpackChunkName: "item-search" */ '@/views/team/');
const Invitation = () => import(/* webpackChunkName: "item-search" */ '@/views/team/invitation/');
const Tabbar = () => import(/* webpackChunkName: "Tabbar" */ '@/vue/components/Tabbar/');


export default [{
	path: "/team",
	name: "team",
	meta: {
		keepAlive: true
	},
	components: { default: Team, tabbar: Tabbar }
}, {
	path: "/team/invitation",
	name: "invitation",
	meta: {
		keepAlive: true
	},
	component: Invitation
}]



