const review = () => import(/* webpackChunkName: "tabbar-cart" */ "@/views/review");
const Tabbar = () => import(/* webpackChunkName: "Tabbar" */ "@/vue/components/Tabbar/");

export default [{
  path: "/review",
  name: "review",
  meta: {
    keepAlive: true,
  },
  components: { default: review, tabbar: Tabbar }
}];



