const orders = () => import("@/views/order/orders.vue");
const SalesDetails = () => import("@/views/order/sales-details/");
const ViewDetails = () => import("@/views/order/sales-details/view.vue");
const Comments = () => import("@/views/order/comment/");
const ChaseCom = () => import("@/views/order/chase-com/");
const TaskStatus = () => import("@/views/order/task-status/");
const Tabbar = () => import("@/vue/components/Tabbar/");
const CollectUpload = () => import("@/views/order/sales-details/collect-upload.vue");

export default [
  {
    path: "/orders",
    name: "orders",
    meta: {
      keepAlive: true,
    },
    components: { default: orders, tabbar: Tabbar }
  },
  {
    path: "/order/sales-details/:id",
    name: "salesDetails",
    component: SalesDetails,
    props: true
  },
  {
    path: "/order/view-details/:id",
    name: "viewDetails",
    component: ViewDetails,
    props: true
  },
  {
    path: "/items/task-status/:status/:taskId/:redirect/:details",
    name: "task-status",
    props: true,
    component: TaskStatus
  },
  {
    path: "/order/comment/:id",
    name: "comment",
    component: Comments,
    props: true
  },
  {
    path: "/order/chase-com/:id",
    name: "chase-com",
    component: ChaseCom,
    props: true
  },
  {
    path: "/order/collectUpload/:id",
    name: "collectUpload",
    component: CollectUpload,
    props: true
  }
];



