const tasks = () => import("@/views/task/index.vue");
const taskViews = () => import("@/views/task/tasks-details/");
const orderViews = () => import("@/views/task/order-details/");
const Tabbar = () => import("@/vue/components/Tabbar/");
const uploadNew = () => import("@/views/task/tasks-details/upload-new.vue");

export default [
  {
    path: "/task",
    name: "tasks",
    meta: {
      keepAlive: true,
    },
    components: { default: tasks, tabbar: Tabbar }
  },
  {
    path: "/task/tasks-details/:id",
    name: "taskViews",
    component: taskViews,
    props: true
  },

  {
    path: "/task/order-details/:id",
    name: "orderViews",
    component: orderViews,
    props: true
  },

  {
    path: "/task/tasks-details/:id",
    name: "uploadNew",
    component: uploadNew,
    props: true
  }
];



