const tab_user = () => import('@/views/user/tabbar-user');
const UserInformation = () => import('@/views/user/user-information-set/');
const UserInfo_SetBg = () => import('@/views/user/user-information-set/set-bg/');
const UserInfo_SetMobile = () => import('@/views/user/user-information-set/set-mobile/');
const UserInfo_SetNickname = () => import('@/views/user/user-information-set/set-nickname/');
const UserInfo_SetPassword = () => import('@/views/user/user-information-set/set-password/');
const UserInfo_SetAge = () => import('@/views/user/user-information-set/set-age/');
const UserInfo_SetArea = () => import('@/views/user/user-information-set/set-area/');
const UserInfo_SetBank = () => import('@/views/user/user-information-set/set-bank/');
const UserInfo_SetQQ = () => import('@/views/user/user-information-set/set-qq/');
const UserInfo_SetWechat = () => import('@/views/user/user-information-set/set-wechat/');
const UserInfo_ID= () => import('@/views/user/user-id/');
const UserInfo_Taobao= () => import('@/views/user/user-taobao/');
const EarningsList= () => import('@/views/user/earnings-list/');
const WithdrawList= () => import('@/views/user/withdraw-list/');
const Withdraw= () => import('@/views/user/withdraw/');
const Tabbar = () => import('@/vue/components/Tabbar/');

export default [
  {
    path: "/user",
    name: "user",
    meta: {
    keepAlive: true
    },
    components: {default: tab_user, tabbar: Tabbar }
  },
  {
    path: "/user/information",
    name: "user-information",
    component: UserInformation
  },
  {
    path: "/user/information/setbg",
    name: "user-info-setbg",
    component: UserInfo_SetBg
  },
  {
    path: "/user/information/setMobile",
    name: "user-info-setMobile",
    component: UserInfo_SetMobile
  },
  {
    path: "/user/information/setNickname",
    name: "user-info-setNickname",
    component: UserInfo_SetNickname
  },
  {
    path: "/user/information/setAge",
    name: "user-info-setAge",
    component: UserInfo_SetAge
  },
  {
    path: "/user/information/setArea",
    name: "user-info-setArea",
    component: UserInfo_SetArea
  },
  {
    path: "/user/information/setBank",
    name: "user-info-setBank",
    component: UserInfo_SetBank
  },
  {
    path: "/user/information/setPassword",
    name: "user-info-setPassword",
    component: UserInfo_SetPassword
  },
  {
    path: "/user/information/setQQ",
    name: "user-info-setQQ",
    component: UserInfo_SetQQ
  },
  {
    path: "/user/information/setWechat",
    name: "user-info-setWechat",
    component: UserInfo_SetWechat
  },
  {
    path: "/user/information/id",
    name: "user-info-id",
    component: UserInfo_ID,
  },
  {
    path: "/user/information/taobao",
    name: "user-info-taobao",
    component: UserInfo_Taobao,
  },
  {
    path: "/user/earnings/list",
    name: "user-earnings-list",
    component: EarningsList
  },
  {
    path: "/user/withdraw/list",
    name: "user-order-ele-list",
    component: WithdrawList
  },
  {
    path: "/user/withdraw",
    name: "withdraw",
    component: Withdraw,
  }
]



