import axios from 'axios';
import qs from 'qs';
import { Toast, Dialog } from 'vant';
import router from '@/vue/router/';

let api_domain = "http://dt.kejic.net/"

let instance = axios.create({
	timeout: 60000,
  //FIXME 测试
  baseURL: process.env.NODE_ENV === "development" ? '/api' : api_domain
});

instance.interceptors.request.use( (config) => {
  !config.hideLoading && Toast.loading({mask: true, duration: 0});
	let token = localStorage.token;
	if(token){
	  config.headers['Auth-Token'] = token;
  }
	return config;
}, (err) => {
	return Promise.reject(err);
});

instance.interceptors.response.use( (res) => {
  Toast.clear();
	return res;
}, (error) => {
    Toast.clear();
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          let msg = error.response.data.msg;
          let code = error.response.data.code;
          if(code === 40014){
            Dialog.alert({
              title: "提示",
              message: `当前进行中的任务太多了，请完成之后，再接任务哦~！
              （签收、确认收货并好评收到佣金之后，即算完成任务哦！没有物流信息的，请发货后5天后再签收、确认收货）`
            })
          }else if(code === 50020){
            Dialog.alert({
              title: "提示",
              message: `请在tb付款后再点击【确认付款】；若失败，请刷新页面再尝试即可。（付款的订单号必须和提交的订单号相同）`
            })
          }else if(code === 50021){
            Dialog.alert({
              title: "提示",
              message: `请物流签收并在 tb收货 之后，再来点击【确认收货】按钮哦~！（没有物流信息的，请发货后5天后再收货）`
            })
          }else{
            Toast.fail(msg);
          }
          if(error.response.data.code === 10001){
            setTimeout(() => {
              localStorage.removeItem('token')
              router.push({path: 'login'});
            }, 500)
          }
          break;
        case 500:
          Toast.fail('抱歉，服务端异常');
          break;
        default:
          Toast.fail('未知错误！！');
          break;
      }
    }
    return Promise.reject(error);
});

const post = (url, data, config = {}) => {
	return instance.post(url, data, config)
}

const put = (url, data, config = {}) => {
	return instance.put(url, data, config)
}

const get = (url, params, config = {}) => {
	return instance.get(url, {
		params: params,
		...config
	})
}

const deleteMethod = (url, config = {}) => {
	return instance({
		url: url,
		method: 'delete',
		...config
	})
}

export default {
	install(Vue) {
		Object.defineProperties(Vue.prototype, {
			$reqGet: {
				value: get
			},
			$reqPost: {
				value: post
			},
			$reqPut: {
				value: put
			},
			$reqDel: {
				value: deleteMethod
			},
		})
	}
}



