import {USER_PROFILE ,USER_INFO} from "@/api/user"
import {TASK_LIST} from "@/api/task"
import { Toast } from 'vant';
import {getLocalStorage, setLocalStorage} from '@/assets/js/util';
import Vue from 'vue';

const profile = (callback) => {
    let _this = this;
    Vue.prototype.$reqGet(USER_PROFILE).then(res => {
      getSuccess(res, callback)
    }).catch(err => {
      getSuccess(err, callback)
    })
}

let getSuccess = (res, callback) => {
  if(!res.data){
    return;
  }
  if(res.data.code != 0){
    let msg = (res.data && res.data.msg) ? res.data.msg : "加载用户资料失败";
    Toast.fail(msg);
    return;
  }
  const data = res.data.data;
  callback(data);
}

const getTaskList = (callback) => {
  Vue.prototype.$reqGet(TASK_LIST).then(res => {
    getTasksSuccess(res, callback);
  }).catch(err => {
    getTasksSuccess(err, callback)
  });
};

let getTasksSuccess = (res, callback) => {
  if(!res.data || res.data.code != 0){
    return;
  }
  let comments_task = res.data.data.comments;
  let sales_task = res.data.data.sales;
  callback(comments_task, sales_task);
};
const getUserInfo = (callback) => {
  Vue.prototype.$reqGet(USER_INFO).then(res => {
    if(res.data.code==10){
        window.localStorage.removeItem('token');
        window.localStorage.removeItem('account');
        window.localStorage.removeItem('name');
        window.localStorage.removeItem('first_leader');
        window.localStorage.removeItem('superior_id');
        window.localStorage.removeItem('superior_name');
        window.localStorage.removeItem('invite_auth');
        this.$router.push({name: 'login'});
        return;
    }
    if(!res.data || res.data.code != 0){
      return;
    }
    callback(res.data.data);
  }).catch(err => {
    if(!err.data || err.data.code != 0){
      return;
    }
    callback(err.data.data);
  });
};

export default {
  install(Vue) {
    Object.defineProperties(Vue.prototype, {
      $user:{
        value: {
          getTaskList,
          getUserInfo
        }
      }
    })
  }
}



