const history = () => import(/* webpackChunkName: "tabbar-cart" */ "@/views/history");
const Tabbar = () => import(/* webpackChunkName: "Tabbar" */ "@/vue/components/Tabbar/");

export default [{
  path: "/history",
  name: "history",
  meta: {
    keepAlive: true,
  },
  components: { default: history, tabbar: Tabbar }
}];



