const message = () => import("@/views/message/index.vue");
const noticeList = () => import("@/views/message/notice-list/");
const noticeViews = () => import("@/views/message/notice-details/");
const workViews = () => import("@/views/message/work-details/");
const workEdit = () => import("@/views/message/work-edit/");
const Tabbar = () => import("@/vue/components/Tabbar/");

export default [
  {
    path: "/message",
    name: "message",
    meta: {
      keepAlive: true,
    },
    components: { default: message, tabbar: Tabbar }
  },
  {
    path: "/message/notice-details/:id",
    name: "noticeViews",
    component: noticeViews,
    props: true
  },{
    path: "/message/notice-list",
    name: "noticeList",
    components: { default: noticeList, tabbar: Tabbar },
    props: true
  },

  {
    path: "/message/work-details/:id",
    name: "workViews",
    component: workViews,
    props: true
  },
  {
    path: "/message/work-edit",
    name: "workEdit",
    component: workEdit,
    props: true
  },

];



