import '@/assets/scss/iconfont/iconfont.css';
import Vue from 'vue';
import App from './App.vue';

import router from '@/vue/router/';
import Vuelidation from '@/vue/plugins/vuelidation';
import valid from '@/vue/mixin/valid';
import VueCountdown from '@/vue/plugins/vue-countdown';
import 'babel-polyfill';
import VueClipboard from  'vue-clipboard2';
Vue.use(VueClipboard)

import {
	Actionsheet,
	Waterfall,
	Lazyload,
	Toast,
	Tag,
	Dialog,
	Cell,
	CellGroup,
	Field,
	Icon,
	Button,
	Popup,
	loading,
	List,
	Collapse,
	CollapseItem,
	NavBar
} from 'vant';

import axios from '@/vue/plugins/axios';
import util from '@/assets/js/util';
import pull from '@/api/pull';
import filters from "@/vue/filter/";
import eventbus from "@/vue/event-bus";


// 判断是否通过微信访问

var agent = navigator.userAgent.toLowerCase();
// if (agent.match(/MicroMessenger/i) != "micromessenger") {
//   window.location.href = "#/home/wxerror";
// }

// plugins
Vue.use(VueCountdown);
Vue.use(axios);
Vue.use(Vuelidation);
Vue.use(valid);
Vue.use(eventbus);

// vue
Vue.use(filters);
Vue.use(util);
Vue.use(pull);

// vant
Vue.use(Waterfall);
Vue.use(Lazyload, {
	preLoad: 1.3,
	error: '/static/img/goods_default.png',
	loading: '/static/img/goods_default.png',
	attempt: 1,
	listenEvents: ['scroll'],
	lazyComponent: true,
});
Vue.use(Tag);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Field);
Vue.use(Icon);
Vue.use(Button);
Vue.use(Popup);
Vue.use(loading);
Vue.use(List);
Vue.use(NavBar);
Vue.use(Collapse).use(CollapseItem);
Vue.use(Actionsheet);
Toast.setDefaultOptions({
	duration: 1500
})

new Vue({
	el: '#app',
	router,
	render: h => h(App)
});



